@import url("./reset.css");

.product {
  display: flex;
}

.product__wrapper {
  width: 953px;
}

.card__content {
  display: flex;
  margin-bottom: 38px;
  gap: 24px;
}

.card__image-product {
  width: 242px;
}

.card__information {
  width: 607px;
  display: flex;
  flex-direction: column;
  row-gap: 8px;
}

.series {
  color: rgba(26, 31, 22, 0.5);
}

.price {
  font-size: 31px;
  line-height: 43px;
}

.text {
  font-size: 20px;
  line-height: 27px;
}

.card__btn-wrapper {
  display: flex;
  justify-content: flex-end;
}

.add__btn {
  width: 178px;
  display: flex;
  padding: 8px 24px;
  justify-content: space-between;
  align-items: center;
  font-family: Open Sans;
  font-size: 20px;
  line-height: 27px;
  color: #fff;
  background: #1a1f16;
  border-radius: 14px;
}

.card__line {
  width: 858px;
  margin-bottom: 33px;
}

.vertical__line {
  height: 921px;
  margin-top: 51px;
}

.description {
  max-width: 873px;
  margin-top: 8px;
  font-size: 20px;
  font-weight: 400;
  line-height: 27px;
  color: rgb(96, 105, 92);
}

.bag {
  padding: 41px 33px 0 85px;
  text-align: center;
}

.bag-list {
  width: 224px;
  display: flex;
  flex-wrap: wrap;
  gap: 16px;
  margin-bottom: 10px;
}

.bag__button-img {
  text-align: -webkit-center;
}

.bag__img {
  width: 64px;
  height: 64px;
}

.bag__button {
  display: flex;
  justify-content: space-between;
  width: 163px;
  padding: 8px 24px;
  border-radius: 14px;
  background: rgb(26, 31, 22);
  font-family: Open Sans;
  font-size: 20px;
  line-height: 27px;
  color: rgb(255, 255, 255);
}
